/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getMessageViewGridConfig } from '../../configs/grid-configs';
import { getCharacterSpeechBubbleConfig } from '../../configs/speech-bubble-configs';
import { ModelEvents } from '../../events/model-events';
import { makePixel } from '../../utils';
import { CharacterComponent } from './character-component';

export class MessageView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    super.build(this.getGridConfig());

    this._build();
    this.hide(true);
    lego.event.on(ModelEvents.GameModel.ShowCharacterMessageUpdate, this._onShowCharacterMessageUpdate, this);
    lego.event.on(ModelEvents.CtaModel.PreVisibleUpdate, this.destroy, this);
  }

  get name() {
    return 'MessageView';
  }

  getGridConfig() {
    return getMessageViewGridConfig();
  }

  hide(force = false) {
    if (force) {
      this.visible = false;
      this.alpha = 0;
    } else {
      this.game.add
        .tween(this)
        .to({ alpha: 0 }, 600, Phaser.Easing.Cubic.InOut, true)
        .onComplete.add(() => {
          this.visible = false;
        });
    }
  }

  show() {
    this.game.add
      .tween(this)
      .to({ alpha: 1 }, 400, Phaser.Easing.Cubic.InOut, true)
      .onStart.add(() => {
        this.visible = true;
        this.alpha = 0;
        this._character.show();
      });
  }

  _onShowCharacterMessageUpdate(value) {
    value ? this.show() : this.hide();
  }

  _build() {
    this._buildBlocker();
    this._buildCharacter(getCharacterSpeechBubbleConfig());
  }

  _buildBlocker() {
    this._blocker = makePixel({ alpha: 0.3 });
    this._blocker.name = this.name;
    this.setChild('blocker', this._blocker);
  }

  _buildCharacter(bubbleConfig) {
    this._character = new CharacterComponent(bubbleConfig);
    this.setChild('character', this._character);
  }
}
