import { CellAlign, CellScale } from '@armathai/phaser2-grid';

export function getMessagesViewGridLandscapeConfig() {
  return {
    // debug: { color: 0x0000ff },
    name: 'tutorial',
    cells: [
      {
        name: 'blocker',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      },
      {
        name: 'character',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        align: CellAlign.LeftBottom,
        offset: { y: 20 }
      }
    ]
  };
}

