import { getBoardCatSpineConfig, Spines } from '../../../../configs/spine-configs';
import { makeSpine } from '../../../../utils';
import { Container } from '../../../../utils/container';

export class CatItemComponent extends Container {
  constructor() {
    super();
    const spine = makeSpine(getBoardCatSpineConfig(0, 30));
    spine.setAnimationByName(0, Spines.BoardCat.Animations.Animation, true);

    this.addChild(spine);
    this.scale.set(0.6);
  }
}
