/* eslint-disable class-methods-use-this */
import { makeSpeechBubble, makeSpine, postRunnable } from '../../utils';
import { Container } from '../../utils/container';

export class CharacterComponent extends Container {
  constructor(config) {
    super();
    this._showTween = null;
    this._hideTween = null;
    this._animations = config.character.animations;

    this.addChild((this._character = this._buildCharacter(config.character)));
    this.addChild((this._speechBubble = this._buildSpeechBubble(config)));

    this.alpha = 0;
  }

  destroy() {
    this.game.tweens.remove(this._showTween);
    this.game.tweens.remove(this._hideTween);

    super.destroy();
  }

  show() {
    this.game.add.tween(this).to({ alpha: 1 }, 300, Phaser.Easing.Linear.None, true);
    this._showTween = this.game.add.tween(this._character).to({ x: 0 }, 300, Phaser.Easing.Linear.None);
    postRunnable(() => {
      this._character.setToSetupPose();
      this._character.setAnimationByName(0, this._animations.start, false);
      this._character.addAnimationByName(0, this._animations.idle, true);
      this._character.x -= Math.abs(this._character.width) * 2;
      this._showTween.start();

      this.game.add.tween(this._speechBubble).from({ angle: -45 }, 600, Phaser.Easing.Back.Out, true, 200);
      this.game.add.tween(this._speechBubble).from({ alpha: 0 }, 800, Phaser.Easing.Cubic.Out, true);
      this.game.add.tween(this._speechBubble.scale).from({ x: 0.9, y: 0.9 }, 600, Phaser.Easing.Elastic.Out, true, 400);
    });

    return this._showTween;
  }

  hide() {
    this._hideTween = this.game.add.tween(this).to({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true);
    return this._hideTween;
  }

  _buildCharacter(characterConfig) {
    const character = makeSpine(characterConfig.spine);
    character.scale.set(characterConfig.scale);
    return character;
  }

  _buildSpeechBubble({ text, position }) {
    const speechBubble = makeSpeechBubble(text);
    speechBubble.pivot.set(-speechBubble.width / 2, speechBubble.height / 2);
    speechBubble.x = position.x;
    speechBubble.y = position.y;

    return speechBubble;
  }
}
