import { lego } from '@armathai/lego';
import { getMergeItems } from '../../models/game/board/board-model';
import { store } from '../../models/store';
import { itemsToCtaCompleteCommand } from '../ad/cta/items-to-cta-complete-command';

export function onItemCreatedCommand(uuid) {
  store.game.boardModel.creationComplete(uuid);

  const { type } = store.game.boardModel.getCell(uuid).item;
  if (type === getMergeItems().large) {
    lego.command.execute(itemsToCtaCompleteCommand);
  }
}
