import { lego } from '@armathai/lego';
import { BoardState } from '../../../constants';
import { store } from '../../../models/store';
import { resetHintVisibilityTimerCommand } from './reset-hint-visibility-timer-command';
import { setHintVisibleCommand } from './set-hint-visible-command';
import { stopHintVisibilityTimerCommand } from './stop-hint-visibility-timer-command';

export function updateHintStateCommand() {
  lego.command.payload(false).execute(setHintVisibleCommand);

  if (store.game.boardModel.state === BoardState.Idle) {
    lego.command.execute(resetHintVisibilityTimerCommand);
  } else {
    lego.command.execute(stopHintVisibilityTimerCommand);
  }
}
