import { FONT1 } from '../constants';

// TUTORIAL
export function getTutorialTextConfig(text, width) {
  return {
    text: {
      text: `${text}`,
      ignoreLocalization: false,
      toString: function toString() {
        return this;
      }
    },
    style: {
      font: FONT1,
      fontSize: 30,
      fill: '#fafefa',
      align: 'center',
      stroke: '#000000',
      strokeThickness: 3,
      lineSpacing: -8,
      wordWrap: true,
      wordWrapWidth: width
    }
  };
}

