import { ItemsConfig } from '../../../configs/items-config';
import { ObservableModel } from '../../observable-model';

export const ItemType = {
  candy: 'candy',
  boots: 'boots',
  coffee: 'coffee',
  sticks: 'sticks',
  house: 'house',
  christmas1: 'christmas1',
  christmas2: 'christmas2',
  tree: 'tree'
};

export class ItemModel extends ObservableModel {
  constructor(type) {
    super('ItemModel');

    const { enabled } = ItemsConfig[type];
    this._type = type;
    this._remove = false;
    this._highlight = false;
    this._alert = false;
    this._enabled = enabled;
    this._magnet = null;
    this._hide = false;

    this.makeObservable('_remove', '_highlight', '_alert', '_hide', '_magnet');
  }

  get type() {
    return this._type;
  }

  get remove() {
    return this._remove;
  }

  set remove(value) {
    this._remove = value;
  }

  get enabled() {
    return this._enabled;
  }

  get alert() {
    return this._alert;
  }

  set alert(value) {
    this._alert = value;
  }

  get highlight() {
    return this._highlight;
  }

  set highlight(value) {
    this._highlight = value;
  }

  get hide() {
    return this._hide;
  }

  set hide(value) {
    this._hide = value;
  }

  get magnet() {
    return this._magnet;
  }

  set magnet(value) {
    this._magnet = value;
  }
}
