/* eslint-disable class-methods-use-this */
import { makeText } from '../../../utils';
import { Rhombus } from '../../../utils/geom/rhombus';

const WIDTH = 148;
const HEIGHT = 88;

export class CellView extends Phaser.Graphics {
  constructor(row, col, uuid) {
    super(CI_API.game);
    this._uuid = uuid;
    this._row = row;
    this._col = col;
    this.alpha = 0;
    this._shape = new Rhombus(-WIDTH / 2, -HEIGHT / 2, WIDTH, HEIGHT);

    this._build(row, col);
  }

  get uuid() {
    return this._uuid;
  }

  get shape() {
    return this._shape;
  }

  get row() {
    return this._row;
  }

  get col() {
    return this._col;
  }

  _build(row, col) {
    const text = makeText({
      text: {
        text: `${row},${col}`,
        ignoreLocalization: true,
        toString: function toString() {
          return this;
        }
      }
    });

    this.beginFill(Math.random() * 0xffffff);
    this.drawPolygon(this._shape.points);
    this.endFill();
    text.anchor.set(0.5);
    this.addChild(text);
  }
}
