import { GameOverReasons } from '../../../constants';
import { store } from '../../../models/store';

export function itemsToCtaCompleteCommand() {
  store.game.startShowQuestCompleteView();

  store.ad.cta.show(GameOverReasons.ItemsToCtaReached, CI_API.Globals.PARAMS.skip_cta_delay ? 3000 : 6000);
}
