/* eslint-disable max-classes-per-file */
/* eslint-disable class-methods-use-this */
import { getUnlockTutorialButtonConfig } from '../../configs/button-configs';
import {
  getArrowImageConfig,
  getHandImageConfig,
  getTutorialCharacterImageConfig,
  getTutorialGroundImageConfig
} from '../../configs/image-configs';
import { getTutorialBgPatchConfig } from '../../configs/ninepatch-configs';
import { getTutorialTextConfig } from '../../configs/text-configs';
import Globals from '../../kernel/globals';
import { fitText, makeImage, makeNinePatch, makeText } from '../../utils';
import { Button } from '../../utils/button/button';
import { Container } from '../../utils/container';
import { BootsItemComponent } from '../game/board/items/boots-item-component';
import { CandyItemComponent } from '../game/board/items/candy-item-component';
import { CupItemComponent } from '../game/board/items/cup-item-component';

const tutorialConfig = {
  'sleeping beauty': {
    item: CupItemComponent,
    character: 'sleeping_beauty'
  },
  'puss in boots': {
    item: BootsItemComponent,
    character: 'cats_in_boots'
  },
  Rapunzel: {
    item: CandyItemComponent,
    character: 'rapunzel'
  }
};

class CompoComponent extends Phaser.Group {
  constructor(ItemClass) {
    super(CI_API.game);
    this._build(ItemClass);
  }

  _build(ItemClass) {
    this._buildFundament();
    this._buildElements(ItemClass);
    this._buildHand();
  }

  _buildFundament() {
    const ground1 = makeImage(getTutorialGroundImageConfig());
    const ground2 = makeImage(getTutorialGroundImageConfig());
    ground2.scale.set(-1);
    ground1.y = 42;
    ground1.x = 67;

    this.addChild(ground1);
    this.addChild(ground2);
  }

  _buildElements(ItemClass) {
    const element1 = new ItemClass();
    const element2 = new ItemClass();
    const element3 = new ItemClass();

    element1.scale.set(0.85);
    element2.scale.set(0.85);
    element3.scale.set(0.85);

    element1.position.set(10, -10);
    element2.position.set(75, 40);
    element3.position.set(-20, 80);

    this.addChild(element1);
    this.addChild(element2);
    this.addChild(element3);
  }

  _buildHand() {
    const hand = makeImage(getHandImageConfig());

    hand.anchor.set(1, 0);
    hand.position.set(-20, 80);
    hand.scale.set(0.85);

    this.addChild(hand);
  }
}

export class TutorialSequenceView extends Container {
  constructor(config) {
    super();
    this._config = config;
    this._showTween = null;
    this._hideTween = null;
    this.clickButton = new Phaser.Signal();

    const characterConfig = tutorialConfig[Globals.PARAMS.tutorial_character];

    this.addChild((this._bg = this._buildBg()));
    this.addChild((this._label = this._buildLabel(config.text)));
    this.addChild((this._character = this._buildCharacter(characterConfig.character)));
    this.addChild((this._comboComponent = this._buildComboComponent(characterConfig.item)));
    this.addChild((this._arrow = this._buildArrow()));
    this.addChild((this._button = this._buildButton()));

    this._bg.width = 580;
    this._bg.height = 500;

    this._arrow.scale.set(0.8);
    this._arrow.y = -150;
    this._arrow.x = 20;

    this._button.y = 180;

    this._label.anchor.set(0.5);
    this._label.y = 90;

    this._character.anchor.set(0.5);
    this._character.x = 175;
    this._character.y = -100;

    this._comboComponent.scale.set(0.8);
    this._comboComponent.position.set(-160, -120);

    this._button.buttonPulsation();
    this._button.onClick.addOnce(() => {
      this.clickButton.dispatch();
    });

    this.alpha = 0;
  }

  rebuild() {
    this._button.buttonPulsation();
  }

  destroy() {
    this.game.tweens.remove(this._showTween);
    this.game.tweens.remove(this._hideTween);

    super.destroy();
  }

  show() {
    this._showTween = this.game.add.tween(this).to({ alpha: 1 }, 300, Phaser.Easing.Linear.None, true);
    return this._showTween;
  }

  hide() {
    const { y } = this;
    this._hideTween = this.game.add.tween(this).to({ alpha: 0, y: y + 500 }, 1000, Phaser.Easing.Linear.None, true);
    return this._hideTween;
  }

  _buildBg() {
    return makeNinePatch(getTutorialBgPatchConfig());
  }

  _buildLabel(text) {
    if (this._label) {
      this._label.destroy();
      this._label = null;
    }

    const { width, height } = this._bg;
    const label = makeText(getTutorialTextConfig(text, width * 0.7));
    fitText(label, width * 0.9, 90);
    return label;
  }

  _buildComboComponent(itemClass) {
    return new CompoComponent(itemClass);
  }

  _buildArrow() {
    return makeImage(getArrowImageConfig());
  }

  _buildCharacter(characterFrame) {
    return makeImage(getTutorialCharacterImageConfig(characterFrame));
  }

  _buildButton() {
    return new Button(getUnlockTutorialButtonConfig());
  }
}
