import { delayRunnable, removeRunnable } from '../../utils';
import { ObservableModel } from '../observable-model';
import { BoardModel } from './board/board-model';

export class GameModel extends ObservableModel {
  constructor() {
    super('GameModel');

    this._boardModel = null;
    this._showUnlockMessage = false;
    this._showCharacterMessage = false;
    this._showQuestCompleteView = false;

    this.makeObservable();
  }

  get boardModel() {
    return this._boardModel;
  }

  get showUnlockMessage() {
    return this._showUnlockMessage;
  }

  set showUnlockMessage(value) {
    this._showUnlockMessage = value;
  }

  get showCharacterMessage() {
    return this._showCharacterMessage;
  }

  get showQuestCompleteView() {
    return this._showQuestCompleteView;
  }

  startShowCharacterMessage() {
    this._showCharacterMessage = true;
    this._characterMessageRunnable = delayRunnable(2000, () => {
      this._showCharacterMessage = false;
      this._boardModel.startSecondSequence();
    });
  }

  startShowQuestCompleteView() {
    this._showQuestCompleteView = true;
    this._characterMessageRunnable = delayRunnable(5000, () => {
      this._showQuestCompleteView = false;
    });
  }

  initialize() {
    this._boardModel = new BoardModel();
    this._boardModel.initialize();
  }

  destroy() {
    removeRunnable(this._characterMessageRunnable);
    this._boardModel.destroy();
    this._boardModel = null;
  }
}
