import { lego } from '@armathai/lego';
import { getHandImageConfig } from '../../configs/image-configs';
import { CellState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { getMergeItems } from '../../models/game/board/board-model';
import { store } from '../../models/store';
import { delayRunnable, getViewByProperty, makeImage, removeRunnable } from '../../utils';
import { Container } from '../../utils/container';
import { ItemView } from '../game/board/item-view';

const autoMergeHintCount = 5;

export class HintView extends Container {
  constructor() {
    super();

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    lego.event.on(ModelEvents.CtaModel.VisibleUpdate, this.destroy, this);
    this._hide();
    this._buildHand();
  }

  destroy() {
    lego.event.off(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    lego.event.off(ModelEvents.CtaModel.VisibleUpdate, this.destroy, this);
    this._hide();

    super.destroy();
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  _show() {
    this._hintsCount = 0;

    const { core, small } = getMergeItems();

    this._targetCells = store.game.boardModel
      .getCellsByState(CellState.Idle)
      .filter(c => (c.item && c.item.type === core) || (c.item && c.item.type === small));

    lego.event.emit(ViewEvents.HintView.Start, this._targetCells);

    let indexFrom;
    let indexTo;

    if (this._targetCells[0].item.type === small) {
      indexFrom = 0;
      indexTo = this._targetCells.length - 1;
    } else {
      indexFrom = this._targetCells.length - 1;
      indexTo = 0;
    }

    const fromCell = this._targetCells[indexFrom];
    const toCell = this._targetCells[indexTo];

    const boardView = getViewByProperty('name', 'BoardView');

    const item = new ItemView('fakeIcon', fromCell.item.type);
    this.addChildAt((this._item = item), 0);
    const { x: fromX, y: fromY } = boardView.getItem(fromCell.item.uuid);
    const { x: toX, y: toY } = boardView.getItem(toCell.item.uuid);

    const pos = this.parent.toLocal(boardView.toGlobal(new Phaser.Point(fromX, fromY)), undefined);

    this._startMoveTween(pos, toX, toY, fromX, fromY, fromCell, toCell);
  }

  _startMoveTween(pos, toX, toY, fromX, fromY, fromCell, toCell) {
    this._hintsCount += 1;
    const dx = toX - fromX;
    const dy = toY - fromY;
    this.position.set(pos.x, pos.y);
    this._hand.alpha = 1;
    this.visible = true;
    this.game.tweens.removeFrom(this);
    this.game.tweens.removeFrom(this._hand);
    this.game.add.tween(this._hand).from({ alpha: 0 }, 200, Phaser.Easing.Cubic.InOut, true);
    this._handMoveTwn = this.game.add
      .tween(this._hand)
      .from({ x: -dx / 2, y: -dy / 2 }, 800, Phaser.Easing.Cubic.Out, true);

    this._handMoveTwn.onComplete.add(() => {
      this._moveTwn = this.game.add
        .tween(this)
        .to({ x: pos.x + dx, y: pos.y + dy }, 1000, Phaser.Easing.Cubic.Out, true);

      this._moveTwn.onStart.addOnce(() => {
        if (this._hintsCount === autoMergeHintCount && CI_API.Globals.PARAMS.auto_merge) {
          lego.event.emit(ViewEvents.HintView.LastHintStart, fromCell, toCell);
        }
      });

      this._moveTwn.onComplete.addOnce(() => {
        this.game.add.tween(this._hand).to({ alpha: 0 }, 200, Phaser.Easing.Cubic.InOut, true);
        removeRunnable(this._hideRunnable);

        if (this._hintsCount === autoMergeHintCount && CI_API.Globals.PARAMS.auto_merge) {
          lego.event.emit(ViewEvents.HintView.LastHintComplete, fromCell, toCell);
        } else {
          this._hideRunnable = delayRunnable(500, () => {
            this._startMoveTween(pos, toX, toY, fromX, fromY, fromCell, toCell);
          });
        }
      });
    });
  }

  _hide() {
    this.game.tweens.removeFrom(this);
    this.game.tweens.remove(this._handMoveTwn);
    this.game.tweens.remove(this._moveTwn);
    removeRunnable(this._hideRunnable);
    if (this._hand) {
      this._hand.position.set(0, 0);
      this.game.tweens.removeFrom(this._hand);
    }
    this.visible = false;
    this._item && this._item.destroy();
    lego.event.emit(ViewEvents.HintView.Stop, this._targetCells);
  }

  _buildHand() {
    const hand = makeImage(getHandImageConfig());
    this.addChild((this._hand = hand));
    hand.anchor.set(0.9, 0.1);
  }
}
