import { ItemsConfig } from '../../../configs/items-config';
import { BoardState, CellState } from '../../../constants';
import { difference } from '../../../utils/array/difference';
import { sample } from '../../../utils/array/sample';
import { sampleSize } from '../../../utils/array/sample-size';
import { bunchClusters, findClustersByType, getBunchOfCell, getCellsFromBunch } from '../../../utils/cluster';
import { clusterFilter } from '../../../utils/cluster/filters';
import { objectKeys } from '../../../utils/object/object-keys';
import { objectValues } from '../../../utils/object/object-values';
import { ObservableModel } from '../../observable-model';
import { CellModel } from './cell-model';
import { ItemModel, ItemType } from './item-model';

const WIDTH = 5;
const HEIGHT = 6;

const CoreItems = {
  'sleeping beauty': 'coffee',
  'puss in boots': 'boots',
  Rapunzel: 'candy'
};

export function getCoreItemType() {
  return CoreItems[CI_API.Globals.PARAMS.tutorial_character];
}

export function getCharacterItemType() {
  const keys = {
    'sleeping beauty': 'beauty',
    'puss in boots': 'puss',
    Rapunzel: 'rapunzel'
  };

  return keys[CI_API.Globals.PARAMS.tutorial_character];
}

export function getMergeItems() {
  const items = {
    'core sticks house': { core: getCoreItemType(), small: 'sticks', large: 'house' },
    'core christmas christmas': { core: getCoreItemType(), small: 'christmasSmall', large: 'christmasLarge' },
    'sticks house': { small: 'sticks', large: 'house' },
    'christmas christmas': { small: 'christmasSmall', large: 'christmasLarge' }
  };

  return items[CI_API.Globals.PARAMS.merge_items];
}

function getInitialItemsConfig() {
  return {
    [getCoreItemType()]: [
      { row: 0, col: 1 },
      { row: 0, col: 2 },
      { row: 2, col: 1 }
    ],
    [ItemType.tree]: [
      { row: 0, col: 0 },
      { row: 2, col: 3 }
    ],
    [sample(objectValues(CoreItems).filter(type => type !== getCoreItemType()))]: [
      { row: 2, col: 0 },
      { row: 3, col: 3 }
    ],
    [getCharacterItemType()]: [{ row: 0, col: 3 }]
  };
}

export class BoardModel extends ObservableModel {
  constructor() {
    super('BoardModel');
    this._cells = null;
    this._state = BoardState.Unknown;
    this._matchLength = 2;
    this._clusters = [];
    this._bunches = [];
    this._mergeEpicenter = null;
    this._characterAppeared = false;
    this.makeObservable('_cells', '_state', '_characterAppeared');
  }

  get cells() {
    return this._cells;
  }

  get state() {
    return this._state;
  }

  get mergeEpicenter() {
    return this._mergeEpicenter;
  }

  get characterAppeared() {
    return this._characterAppeared;
  }

  getItemsLeftCount() {
    const cells = this.getCellsByState(CellState.Idle);
    const items = cells.filter(c => c.item).map(c => c.item);
    const { core, small, large } = getMergeItems();
    let itemsLeft;

    for (let i = 0; i < items.length; i += 1) {
      if (items[i].type === core) {
        itemsLeft = '2';
        break;
      } else if (items[i].type === small) {
        itemsLeft = '1';
        break;
      } else if (items[i].type === large) {
        itemsLeft = '0';
        break;
      } else {
        itemsLeft = '1';
      }
    }

    return itemsLeft;
  }

  setState(state) {
    this._state = state;
  }

  getCell(uuid) {
    for (let i = 0; i < this._cells.length; i += 1) {
      const cell = this._cells[i].find(c => c && c.uuid === uuid);
      if (cell) {
        return cell;
      }
    }

    return null;
  }

  getItem(itemUuid) {
    for (let i = 0; i < this._cells.length; i += 1) {
      const cell = this._cells[i].find(c => c && c.item && c.item.uuid === itemUuid);
      if (cell) {
        const { item } = cell;
        if (item) {
          return item;
        }
      }
    }

    return null;
  }

  getCellByItemUuid(uuid) {
    for (let i = 0; i < this._cells.length; i += 1) {
      const cell = this._cells[i].find(c => c && c.item && c.item.uuid === uuid);
      if (cell) {
        return cell;
      }
    }

    return null;
  }

  getEmptyCells() {
    const cells = [];
    for (let i = 0; i < this._cells.length; i += 1) {
      cells.push(...this._cells[i].filter(c => c && c.isEmpty === true));
    }

    return cells;
  }

  getCellsByState(state) {
    const cells = [];
    for (let i = 0; i < this._cells.length; i += 1) {
      cells.push(...this._cells[i].filter(c => c && c.state === state));
    }
    return cells;
  }

  moveComplete(uuid) {
    const cell = this.getCell(uuid);
    cell.setState(CellState.Idle);

    const moveCells = this.getCellsByState(CellState.Move);
    if (!moveCells.length) {
      this.setState(BoardState.Idle);
    }
  }

  mergeComplete(uuid) {
    const cell = this.getCell(uuid);
    const { type: mergedItemType } = cell.item;
    cell.item.remove = true;
    cell.removeItem();
    cell.setState(CellState.Idle);

    const mergeCells = this.getCellsByState(CellState.Merge);
    if (!mergeCells.length) {
      this.setState(BoardState.MergeComplete);
      const epicenterCell = this.getCell(this.mergeEpicenter.uuid);

      const nextItemType = ItemsConfig[mergedItemType].next;
      const item = new ItemModel(nextItemType);
      epicenterCell.setItem(item);
      epicenterCell.setState(CellState.Fill);
      this.setState(BoardState.AddItems);
    }
  }

  creationComplete(uuid) {
    const cell = this.getCell(uuid);
    cell.setState(CellState.Idle);

    if (this.getCellsByState(CellState.Fill).length === 0) {
      if (cell.item.type === getMergeItems().small) {
        this.setState(BoardState.Idle);
      } else if (this._state === BoardState.ThrowItems || cell.item.type === getCharacterItemType()) {
        this.setState(BoardState.Disable);
      } else {
        this.setState(BoardState.Idle);
      }
    }

    if (cell.item.type === getCharacterItemType()) {
      this._characterAppeared = true;
    }
  }

  actionDone(from, to, item) {
    this.setState(BoardState.ActionDone);

    if (to.isEmpty) {
      this._moveItemTo(from, to, item);
    } else if (from === to) {
      this._moveItemTo(from, to, item);
    } else if (!to.enabled) {
      this._moveItemTo(from, from, item);
    } else {
      from.removeItem();
      from.setState(CellState.Idle);
      this._updateClustersAndBunches(this._simpleClusterFinder, this, to);

      if (this._bunches.length) {
        const cellsOfBunch = getCellsFromBunch(this._bunches[0], this._cells);
        if (cellsOfBunch[0].item.type === item.type) {
          this.mergeItems(cellsOfBunch, item, to);
        } else {
          this._shiftItems(from, to, item);
        }
      } else {
        this._shiftItems(from, to, item);
      }
    }
  }

  _shiftItems(from, to, item) {
    const emptyNeighbors = this._getNeighbors(to).filter(c => c.isEmpty);

    if (emptyNeighbors.length) {
      const cell = sample(emptyNeighbors);
      from.removeItem();
      from.setState(CellState.Idle);
      this._replaceItems(cell, to, item);
    } else {
      this._replaceItems(from, to, item);
    }
  }

  checkForMatch(cellUuid, itemUuid) {
    const to = this.getCell(cellUuid);
    const draggingItem = this.getItem(itemUuid);

    this._updateClustersAndBunches(this._simpleClusterFinder, this, to);

    if (this._bunches.length) {
      const cells = getCellsFromBunch(this._bunches[0], this._cells);

      if (cells[0].item.type === draggingItem.type) {
        this.startCellsHighlight(cells);
        this.setCellsMagnet(cells, to);

        draggingItem.hide = true;
      }
    } else {
      this.stopCellsHighlight();
      draggingItem.hide = false;
    }
  }

  checkMergeAbleItems(cellUuid, itemUuid) {
    const to = this.getCell(cellUuid);
    const draggingItem = this.getItem(itemUuid);

    this._updateClustersAndBunches(this._simpleClusterFinder, this, to);

    if (this._bunches.length) {
      const cells = getCellsFromBunch(this._bunches[0], this._cells);

      if (cells[0].item.type === draggingItem.type) {
        return true;
      }
    }
  }

  // eslint-disable-next-line class-methods-use-this
  setCellsMagnet(cells, to) {
    cells.forEach(c => {
      if (c.item) {
        c.item.magnet = to.uuid;
      }
    });
  }

  startCellsHighlight(cells = this.getCellsByState(CellState.Idle)) {
    cells.forEach(cell => {
      if (cell.item) cell.item.highlight = true;
    });
  }

  stopCellsHighlight(cells = this.getCellsByState(CellState.Idle)) {
    cells.forEach(cell => {
      if (cell.item) {
        cell.item.highlight = false;
        cell.item.magnet = null;
      }
    });
  }

  _moveItemTo(from, to, item) {
    from.removeItem();
    to.setItem(item);

    from.setState(CellState.Idle);
    to.setState(CellState.Move);

    this.setState(BoardState.Move);
  }

  _replaceItems(from, to, itemFrom) {
    const { item: itemTo } = to;

    from.removeItem();
    to.removeItem();
    to.setItem(itemFrom);
    from.setItem(itemTo);

    from.setState(CellState.Move);
    to.setState(CellState.Move);

    this.setState(BoardState.Move);
  }

  mergeItems(cellsToMerge, mergeItem, epicenter) {
    this._mergeEpicenter = epicenter;
    mergeItem.remove = true;

    cellsToMerge.forEach(c => {
      c.setState(CellState.Merge);
    });
    this.setState(BoardState.MergeStart);
  }

  _getNeighbors(cell) {
    const { row, col } = cell;

    const up = this.cells[row - 1] && this.cells[row - 1][col];
    const upLeft = this.cells[row - 1] && this.cells[row - 1][col - 1];
    const upRight = this.cells[row - 1] && this.cells[row - 1][col + 1];
    const left = this.cells[row][col - 1];
    const right = this.cells[row][col + 1];
    const bottom = this.cells[row + 1] && this.cells[row + 1][col];
    const bottomLeft = this.cells[row + 1] && this.cells[row + 1][col - 1];
    const bottomRight = this.cells[row + 1] && this.cells[row + 1][col + 1];
    const neighbors = [upLeft, up, upRight, right, bottomRight, bottom, bottomLeft, left];

    return neighbors.filter(c => c);
  }

  initialize() {
    this.initializeCells();
    this.createInitialItems();
  }

  initializeCells() {
    const rows = [];

    for (let i = 0; i < HEIGHT; i += 1) {
      const row = [];

      for (let j = 0; j < WIDTH; j += 1) {
        const cell = new CellModel(i, j);
        cell.setState(CellState.Idle);
        row.push(cell);
      }

      rows.push(row);
    }

    this._cells = rows;
  }

  createInitialItems() {
    const { core, small } = getMergeItems();

    const itemsConfig = getInitialItemsConfig();

    objectKeys(itemsConfig).forEach(key => {
      const poses = itemsConfig[key];

      poses.forEach(pos => {
        const { row, col } = pos;
        const cell = this._cells[row][col];
        const type = key === getCoreItemType() ? core || small : key;

        if (key === getCharacterItemType() && core) {
          return;
        }

        const item = new ItemModel(type);
        cell.setItem(item);
        cell.setState(CellState.Fill);
      });
    });

    this.setState(BoardState.AddItems);
  }

  startSecondSequence() {
    let emptyCells;
    const { small } = getMergeItems();

    if (CI_API.Globals.PARAMS.items_spawn === 'random') {
      const allEmptyCells = this.getEmptyCells().filter(c => c.row < 4 && c.col < 4);
      emptyCells = sampleSize(allEmptyCells, 3);
    } else {
      const neighbors = this._getNeighbors(this._cells[0][0]);
      const emptyNeighbors = neighbors.filter(c => c.isEmpty);
      let j = 0;
      while (emptyNeighbors.length < 3) {
        const emptyNeighborsOfNeighbors = this._getNeighbors(neighbors[j]).filter(c => c.isEmpty);
        j += 1;
        emptyNeighbors.push(...difference(emptyNeighborsOfNeighbors, emptyNeighbors));
      }

      emptyCells = emptyNeighbors;
    }

    emptyCells.splice(0, 3).forEach(cell => {
      const item = new ItemModel(small);
      cell.setItem(item);
      cell.setState(CellState.Fill);
    });

    this.setState(BoardState.ThrowItems);
  }

  _simpleClusterFinder(cell, matchLength = this._matchLength) {
    const { type } = cell.item;
    const c = findClustersByType(this._cells, matchLength, clusterFilter, clusterFilter, type);
    const b = bunchClusters(c);
    const clusters = getBunchOfCell(cell, b);
    const bunches = bunchClusters(clusters);

    return { clusters, bunches };
  }

  _updateClustersAndBunches(finder, context, ...args) {
    const { clusters, bunches } = finder.call(context, ...args);
    this._resetClustersAndBunches();

    this._clusters.push(...clusters);
    this._bunches.push(...bunches);
  }

  _resetClustersAndBunches() {
    this._clusters.length = 0;
    this._bunches.length = 0;
  }
}
