import {
  getTutorialBeautySpineConfig,
  getTutorialCatSpineConfig,
  getTutorialRapunzelSpineConfig,
  Spines
} from './spine-configs';
import { getCtaSpeechBubbleTextConfig, getSpeechBubbleTitleTextConfig } from './text-configs';

function getRapunzelSpeechBubbleConfig() {
  return {
    text: {
      width: 400,
      height: 100,
      title: getSpeechBubbleTitleTextConfig('Rapunzel'),
      speech: getCtaSpeechBubbleTextConfig('I need to rest my feet, where did my house go?')
    },
    character: {
      scale: 0.6,
      spine: getTutorialRapunzelSpineConfig(),
      animations: {
        idle: Spines.TutorialRapunzel.Animations.Idle,
        start: Spines.TutorialRapunzel.Animations.Start
      }
    },
    position: {
      x: 80,
      y: 80
    }
  };
}

function getPussInBootsSpeechBubbleConfig() {
  return {
    text: {
      width: 400,
      height: 100,
      title: getSpeechBubbleTitleTextConfig('Puss in Boots'),
      speech: getCtaSpeechBubbleTextConfig('I need a cat nap, where did my house go?')
    },
    character: {
      scale: 0.6,
      spine: getTutorialCatSpineConfig(),
      animations: {
        idle: Spines.TutorialCat.Animations.Idle,
        start: Spines.TutorialCat.Animations.Start
      }
    },
    position: {
      x: 80,
      y: 50
    }
  };
}

function getSleepingBeautySpeechBubbleConfig() {
  return {
    text: {
      width: 400,
      height: 100,
      title: getSpeechBubbleTitleTextConfig('Sleeping Beauty'),
      speech: getCtaSpeechBubbleTextConfig('I would like to take a nap, where did my house go?')
    },
    character: {
      scale: 0.8,
      spine: getTutorialBeautySpineConfig(),
      animations: {
        idle: Spines.TutorialBeauty.Animations.Idle,
        start: Spines.TutorialBeauty.Animations.Start
      }
    },
    position: {
      x: 80,
      y: -220
    }
  };
}

export function getCharacterSpeechBubbleConfig() {
  const configs = {
    'sleeping beauty': getSleepingBeautySpeechBubbleConfig(),
    'puss in boots': getPussInBootsSpeechBubbleConfig(),
    Rapunzel: getRapunzelSpeechBubbleConfig()
  };

  return configs[CI_API.Globals.PARAMS.tutorial_character];
}
