import { lego } from '@armathai/lego';
import { ModelEvents } from '../events/model-events';
import { ViewEvents } from '../events/view-events';

let sound;

function play(id, volume) {
  sound.play(id, volume);
}

function onLoadCompleteUpdate(complete) {
  if (!complete) {
    return;
  }
  sound = soundLoader.addAudioSprite('audio');
}

function onItemCreated() {
  play('build_main', 1);
}

function onQuestCompleteViewShow() {
  play('quest_completed', 1);
}

function onButtonClick() {
  play('button_click', 1);
}

function playGameTheme() {
  play('game-theme', 0.7);
}

export function SoundObservant() {
  lego.event.on(ModelEvents.LoadModel.CompleteUpdate, onLoadCompleteUpdate, this);
  lego.event.on(ViewEvents.BoardView.PlayFXAnimation, onItemCreated, this);
  lego.event.once(ViewEvents.Game.UserInteraction, playGameTheme, this);
  lego.event.on(ViewEvents.QuestCompleteView.Show, onQuestCompleteViewShow, this);
  lego.event.on(ViewEvents.TutorialView.ButtonClick, onButtonClick, this);
  lego.event.on(ViewEvents.UnlockPopupView.NextClick, onButtonClick, this);
}
