import { getBoardBeautySpineConfig, Spines } from '../../../../configs/spine-configs';
import { makeSpine } from '../../../../utils';
import { Container } from '../../../../utils/container';

export class BeautyItemComponent extends Container {
  constructor() {
    super();
    const spine = makeSpine(getBoardBeautySpineConfig(0, 45));
    spine.setAnimationByName(0, Spines.BoardBeauty.Animations.Animation, true);

    this.addChild(spine);
    this.scale.set(-0.23, 0.23);
  }
}
