import { getBackgroundGridLandscapeConfig, getBackgroundGridPortraitConfig } from './grid/background-grid-configs';
import { getCTAStyle1GridLandscapeConfig, getCTAStyle1GridPortraitConfig } from './grid/cta-grid-configs';
import { getForegroundGridLandscapeConfig, getForegroundGridPortraitConfig } from './grid/foreground-grid-configs';
import { getGameGridLandscapeConfig, getGameGridPortraitConfig } from './grid/game-grid-configs';
import { getMainGridLandscapeConfig, getMainGridPortraitConfig } from './grid/main-grid-configs';
import {
  getMessagesViewGridLandscapeConfig,
  getMessagesViewGridPortraitConfig
} from './grid/messages-view-grid-configs';
import {
  getQuestCompleteViewGridLandscapeConfig,
  getQuestCompleteViewGridPortraitConfig
} from './grid/quest-complete-view-grid-configs';
import { getTutorialGridLandscapeConfig, getTutorialGridPortraitConfig } from './grid/tutorial-grid-configs';
import { getUINewGridLandscapeConfig, getUINewGridPortraitConfig } from './grid/ui-new-grid-configs';
import { getUIOldGridLandscapeConfig, getUIOldGridPortraitConfig } from './grid/ui-old-grid-configs';

export function getMainGridConfig() {
  return LP(getMainGridLandscapeConfig, getMainGridPortraitConfig).call(null);
}

export function getForegroundGridConfig() {
  return LP(getForegroundGridLandscapeConfig, getForegroundGridPortraitConfig).call(null);
}

export function getBackgroundGridConfig() {
  return LP(getBackgroundGridLandscapeConfig, getBackgroundGridPortraitConfig).call(null);
}

export function getUIGridConfig() {
  const config =
    CI_API.Globals.PARAMS.landscape_ui === 'new'
      ? LP(getUIOldGridLandscapeConfig, getUIOldGridPortraitConfig).call(null)
      : LP(getUINewGridLandscapeConfig, getUINewGridPortraitConfig).call(null);
  return config;
}

export function getGameGridConfig() {
  return LP(getGameGridLandscapeConfig, getGameGridPortraitConfig).call(null);
}

export function getCTAStyle1GridConfig() {
  return LP(getCTAStyle1GridLandscapeConfig, getCTAStyle1GridPortraitConfig).call(null);
}

export function getTutorialGridConfig() {
  return LP(getTutorialGridLandscapeConfig, getTutorialGridPortraitConfig).call(null);
}

export function getMessageViewGridConfig() {
  return LP(getMessagesViewGridLandscapeConfig, getMessagesViewGridPortraitConfig).call(null);
}

export function getQuestCompleteViewGridConfig() {
  return LP(getQuestCompleteViewGridLandscapeConfig, getQuestCompleteViewGridPortraitConfig).call(null);
}
