import { lego } from '@armathai/lego';
import { BoardState } from '../../constants';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { setHintVisibleCommand } from '../ad/hint/set-hint-visible-command';
import { startHintVisibilityTimerCommand } from '../ad/hint/start-hint-visibility-timer-command';
import { stopHintVisibilityTimerCommand } from '../ad/hint/stop-hint-visibility-timer-command';

export function onBoardStateUpdateCommand(state) {
  switch (state) {
    case BoardState.Idle:
      lego.command.guard(hintModelGuard).execute(startHintVisibilityTimerCommand);
      break;
    case BoardState.Move:
      break;

    default:
      lego.command
        .guard(hintModelGuard)
        .payload(false)
        .execute(setHintVisibleCommand);
      lego.command.guard(hintModelGuard).execute(stopHintVisibilityTimerCommand);
      break;
  }
}
