import { CellAlign, CellScale } from '@armathai/phaser2-grid';

export function getCTAStyle1GridLandscapeConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        // debug: { color: 0x4bdbd4 },
        name: 'popup',
        bounds: { x: 0, height: 1 },
        padding: { x: 0.2, y: 0.03, width: 1 - 2 * 0.2, height: 0.67 },
        align: CellAlign.CenterBottom
        // scale: CellScale.None
      },
      {
        name: 'footer_bar',
        bounds: { x: 0, y: 0.85, width: 1 }
      },
      {
        name: 'characters',
        bounds: { x: 0.015, y: 0.68, width: 0.292 },
        align: CellAlign.CenterBottom
      },
      {
        name: 'play_button',
        bounds: { x: 0.73, y: 0.86, height: 1 - 0.86 - 0.02 },
        align: CellAlign.LeftCenter
      },
      {
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        name: 'blocker',
        scale: CellScale.Fill
      }
    ]
  };
}

