/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getQuestCompleteViewGridConfig } from '../../configs/grid-configs';
import {
  getAvatarCharacterImageConfig,
  getAvatarCircleImageConfig,
  getAvatarGlowImageConfig,
  getCharacterQuestCompleteImageConfig
} from '../../configs/image-configs';
import { getQuestCompleteViewTitleTextConfig } from '../../configs/text-configs';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { Container } from '../../utils/container';
import { makeImage, makePixel, makeText } from '../../utils/index';

const avatarCharacterConfig = {
  'sleeping beauty': {
    frame: 'sleeping_beauty',
    x: 10,
    characterName: 'sleeping_beauty'
  },
  'puss in boots': {
    frame: 'cat_in_boots',
    x: 8,
    characterName: 'puss_in_boots'
  },
  Rapunzel: {
    frame: 'rapunzel',
    x: -10,
    characterName: 'rapunzel'
  }
};

export class QuestCompleteView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    super.build(this.getGridConfig());

    this._build();
    this.hide(true);

    lego.event.on(ModelEvents.GameModel.ShowQuestCompleteViewUpdate, this._onShowQuestCompleteViewUpdate, this);
    lego.event.on(ModelEvents.CtaModel.VisibleUpdate, this.destroy, this);
  }

  // @ts-ignore
  get name() {
    return 'QuestCompleteView';
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    this._character.scale.set(LP(1, -1), 1);
    super.rebuild(config);
    this._character.x += LP(0, this._character.width);
    this._character.y -= LP(0, this._character.height);
  }

  destroy() {
    this.game.tweens.remove(this._glowTween);
    this.game.tweens.removeFrom(this);

    super.destroy();
  }

  getGridConfig() {
    return getQuestCompleteViewGridConfig();
  }

  hide(force = false) {
    if (force) {
      this.visible = false;
      this.alpha = 0;
    } else {
      this.game.add
        .tween(this)
        .to({ alpha: 0 }, 600, Phaser.Easing.Cubic.InOut, true)
        .onComplete.add(() => {
          this.visible = false;
        });
    }
  }

  show() {
    this.game.add
      .tween(this)
      .to({ alpha: 1 }, 400, Phaser.Easing.Cubic.InOut, true, 800)
      .onStart.add(() => {
        lego.event.emit(ViewEvents.QuestCompleteView.Show);
        this.visible = true;
        this.alpha = 0;
      });
  }

  _onShowQuestCompleteViewUpdate(value) {
    value ? this.show() : this.hide();
  }

  _build() {
    this._buildBlocker();
    this._buildTitle();
    this._buildAvatar();
    this._buildCharacter();
  }

  _buildBlocker() {
    this._blocker = makePixel({ alpha: 0.5 });
    this._blocker.name = this.name;
    this.setChild('blocker', this._blocker);
  }

  _buildTitle() {
    const title = makeText(getQuestCompleteViewTitleTextConfig());
    this.setChild('title', title);
  }

  _buildCharacter() {
    const { characterName } = avatarCharacterConfig[CI_API.Globals.PARAMS.tutorial_character];
    this._character = makeImage(getCharacterQuestCompleteImageConfig(characterName));
    this.setChild('character', this._character);
  }

  _buildAvatar() {
    const avatar = new Container();
    const circle = makeImage(getAvatarCircleImageConfig());
    const glow = makeImage(getAvatarGlowImageConfig());
    const { frame, x } = avatarCharacterConfig[CI_API.Globals.PARAMS.tutorial_character];
    const character = makeImage(getAvatarCharacterImageConfig(frame));

    character.anchor.set(0.5, 1);
    character.x = x;
    character.y = 86;

    // glow.tint = 0xf0951f;
    this._glowTween = this.game.make.tween(glow).to({ rotation: Math.PI * 2 }, 5000, null, true, 0, -1);
    // @ts-ignore
    this._glowTween.universal = true;

    avatar.addChild(glow);
    avatar.addChild(circle);
    avatar.addChild(character);

    avatar.getBounds = () => new Phaser.Rectangle(-256, -256, 512, 512);

    this.setChild('avatar', avatar);
  }
}
