import { CellAlign, CellScale } from '@armathai/phaser2-grid';

export function getQuestCompleteViewGridLandscapeConfig() {
  return {
    // debug: { color: 0x0000ff },
    name: 'view',
    cells: [
      {
        name: 'blocker',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      },
      {
        // debug: { color: 0xff0000 },
        name: 'title',
        bounds: { x: 0, y: 0.09, width: 1, height: 0.3 }
      },
      {
        name: 'avatar',
        bounds: { x: 0, y: 0.27, width: 1, height: 0.63 }
      },
      {
        name: 'character',
        bounds: { x: 0.15, y: 0.32, width: 0.2 },
        offset: { y: 10 },
        align: CellAlign.RightTop,
        scale: CellScale.None
      }
    ]
  };
}

