/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getUnlockPopupButtonConfig } from '../../configs/button-configs';
import { getUnlockMessageBgPatchConfig } from '../../configs/ninepatch-configs';
import { getUnlockPopupTextConfig } from '../../configs/text-configs';
import { CharactersNames } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { fitText, makeNinePatch, makeText } from '../../utils';
import { Button } from '../../utils/button/button';
import { Container } from '../../utils/container';

export class UnlockPopupView extends Container {
  constructor() {
    super();

    this._build();

    lego.event.on(ModelEvents.CtaModel.PreVisibleUpdate, this.destroy, this);
    // lego.event.on(ModelEvents.TutorialSequenceModel.CompleteUpdate, this._onTutorialCurrentCompleteUpdate, this);
  }

  destroy() {
    this._btn.destroy();
    this._btn = null;
    super.destroy();
  }

  rebuild() {
    if (this._btn) {
      this._btn.scale.set(1);
      this._btn.buttonPulsation();
    }
  }

  hide(force = false) {
    if (force) {
      this.visible = false;
      this.alpha = 0;
    } else {
      this.game.add
        .tween(this)
        .to({ alpha: 0 }, 400, Phaser.Easing.Cubic.InOut, true)
        .onComplete.add(() => {
          this.visible = false;
          this.destroy();
        });
    }
  }

  show() {
    this.game.add
      .tween(this)
      .to({ alpha: 1 }, 400, Phaser.Easing.Cubic.InOut, true)
      .onStart.add(() => {
        this.visible = true;
      });
  }

  _build() {
    this._buildBg();
    this._buildText();
    this._buildBtn();
  }

  _buildBg() {
    this._bg = makeNinePatch(getUnlockMessageBgPatchConfig());
    this.addChild(this._bg);
  }

  _buildText() {
    this._text = makeText(getUnlockPopupTextConfig(CharactersNames[CI_API.Globals.PARAMS.tutorial_character]));
    this._text.anchor.set(0.5);
    this._text.y = -40;
    fitText(this._text, this._bg.width * 0.8, this._bg.height * 0.8);
    this.addChild(this._text);
  }

  _buildBtn() {
    this._btn = new Button(getUnlockPopupButtonConfig());
    this._btn.y = 45;
    this._btn.onClick.addOnce(() => {
      lego.event.emit(ViewEvents.UnlockPopupView.NextClick);
    });
    this.addChild(this._btn);

    this._btn.buttonPulsation();
  }
}
