/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getTutorialGridConfig } from '../../configs/grid-configs';
import { InputPriority } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { makePixel } from '../../utils/index';
import { TutorialSequenceView } from './tutorial-sequence-view';

export class TutorialView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    super.build(this.getGridConfig());

    this._current = null;
    this.onSequenceReady = new Phaser.Signal();

    lego.event.on(ModelEvents.TutorialModel.CurrentUpdate, this._onTutorialCurrentUpdate, this);
    lego.event.on(ModelEvents.TutorialSequenceModel.CompleteUpdate, this._onTutorialCurrentCompleteUpdate, this);
  }

  get name() {
    return 'TutorialView';
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    this._current && this._current.rebuild();

    super.rebuild(config);
  }

  destroy(destroyChildren = true, soft = false) {
    this._switchScreenInput(false);

    super.destroy(destroyChildren, soft);
  }

  getGridConfig() {
    return getTutorialGridConfig();
  }

  _buildBlocker(alpha) {
    this._blocker = makePixel({ alpha });
    this._blocker.name = this.name;
    this._blocker.inputEnabled = true;
    this._blocker.input.priorityID = InputPriority.Foreground;
    this.setChild('blocker', this._blocker);
  }

  _onTutorialCurrentUpdate(sequence) {
    if (!sequence) {
      return;
    }

    this._buildSequence(sequence);
    this.onSequenceReady.dispatch();
  }

  _onTutorialCurrentCompleteUpdate(complete) {
    if (!complete) {
      return;
    }

    this._switchScreenInput(false);
    this._hide();
    this._current.hide().onComplete.addOnce(() => {
      lego.event.emit(ViewEvents.TutorialView.SequenceHideComplete);
    });
  }

  _hide() {
    this.game.add.tween(this).to({ alpha: 0 }, 900, Phaser.Easing.Linear.None, true);
  }

  _buildSequence(sequence) {
    this._current = new TutorialSequenceView(sequence.config);
    this._current.show().onComplete.addOnce(() => {
      this._switchScreenInput(true);
    });

    this._buildBlocker(0.5);

    this.setChild('popup', this._current);
  }

  _switchScreenInput(enable) {
    this._current.clickButton.remove(this._onTutorialButtonClick, this);
    enable && this._current.clickButton.add(this._onTutorialButtonClick, this);

    // this.game.input.onDown.remove(this._onScreenClick, this);
    // enable && this.game.input.onDown.addOnce(this._onScreenClick, this);

    this._blocker.events.onInputDown.remove(this._onScreenClick, this);
    this._blocker.events.onInputDown.addOnce(this._onScreenClick, this);
  }

  _onScreenClick() {
    lego.event.emit(ViewEvents.TutorialView.ScreenClick);
  }

  _onTutorialButtonClick() {
    lego.event.emit(ViewEvents.TutorialView.ButtonClick);
  }
}
