/* eslint-disable class-methods-use-this */
// eslint-disable-next-line max-classes-per-file
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCtaStyle1PlayButtonConfig, getQuestStartButtonConfig } from '../../configs/button-configs';
import { getCtaCharactersImageConfig, getQuestImageConfig } from '../../configs/image-configs';
import {
  getCtaPopupBgPatchConfig,
  getCtaPopupQuestBgPatchConfig,
  getCtaPopupRibbonPatchConfig
} from '../../configs/ninepatch-configs';
import {
  getCtaPopupRibbonTextConfig,
  getCtaTitleTextConfig,
  getQuestCountTextConfig,
  getQuestTextConfig
} from '../../configs/text-configs';
import { AdStatus, InputPriority } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import Globals from '../../kernel/globals';
import { store } from '../../models/store';
import moduleBindings from '../../module-bindings';
import { fitText, makeGradient, makeImage, makeNinePatch, makePixel, makeText } from '../../utils';
import { Button } from '../../utils/button/button';
import { Container } from '../../utils/container';

const quests = {
  phoenix: {
    iconConfig: { frame: 'phoenix', position: { x: 0, y: 0 } },
    textConfig: getQuestTextConfig('Unlock the phoenix')
  },
  fog: {
    iconConfig: { frame: 'fog', position: { x: 0, y: 0 } },
    textConfig: getQuestTextConfig('Clear the fog')
  },
  chest: {
    iconConfig: { frame: 'chest', position: { x: 10, y: 0 } },
    textConfig: getQuestTextConfig('Find hidden treasure')
  }
};

const questsConfig = [
  quests[Globals.PARAMS.cta_item_left],
  quests[Globals.PARAMS.cta_item_middle],
  quests[Globals.PARAMS.cta_item_right]
];

class Quest extends Phaser.Group {
  constructor(cta, config) {
    super(CI_API.game);

    this._build(cta, config);
  }

  _build(cta, config) {
    const { iconConfig, textConfig } = config;
    const { frame, position } = iconConfig;

    this.addChild((this._bg = this._buildBg()));
    this.addChild((this._icon = this._buildIcon(frame)));
    this.addChild((this._label = this._buildLabel(textConfig)));
    this.addChild((this._button = cta.buildPlayButton(getQuestStartButtonConfig())));

    this._icon.scale.set(LP(0.75, 0.65));
    this._icon.anchor.set(0.5);
    this._icon.y = position.y - 27;
    this._icon.x = position.x;

    this._label.anchor.set(0.5);
    this._label.y = LP(75, 68);
    fitText(this._label, this._bg.width * 0.9, this._bg.height * 0.4);

    this._button.y = LP(128, 130);
    this._button.buttonPulsation();
  }

  _buildBg() {
    const bgContainer = new Container();
    const bg = makeNinePatch(getCtaPopupQuestBgPatchConfig());
    const label = makeText(getQuestCountTextConfig());

    label.anchor.set(0.5);
    label.y += LP(45, 38);

    bgContainer.addChild(bg);
    bgContainer.addChild(label);

    return bgContainer;
  }

  _buildIcon(iconFrame) {
    return makeImage(getQuestImageConfig(iconFrame));
  }

  _buildLabel(textConfig) {
    return makeText(textConfig);
  }

  _buildButton(config) {
    const btn = new Button(config);
    return btn;
  }
}

class CtaPopup extends Phaser.Group {
  constructor(cta) {
    super(CI_API.game);

    this._build(cta);
  }

  _build(cta) {
    this.addChild((this._bg = this._buildBg()));
    this.addChild((this._ribbon = this._buildRibbon()));
    this.addChild((this._title = this._buildTitleText()));
    this.addChild((this._quests = this._buildQuests(cta)));

    this._ribbon.y -= this._bg.height * 0.47;
    this._ribbon.scale.set(LP(1, 1.3));

    this._title.anchor.set(0.5);
    this._title.y -= this._bg.height * 0.35 - LP(10, -5);

    this._quests.y += LP(38, 32);
  }

  _buildBg() {
    return makeNinePatch(getCtaPopupBgPatchConfig());
  }

  _buildRibbon() {
    const ribbonComponent = new Phaser.Group(CI_API.game);

    const ribbon = makeNinePatch(getCtaPopupRibbonPatchConfig());
    ribbon.anchor.set(0.5, 0.5);
    ribbon.scale.set(0.55);
    ribbon.updateTransform();
    ribbonComponent.addChild(ribbon);

    const label = makeText(getCtaPopupRibbonTextConfig());
    label.anchor.set(0.5, 0.5);
    fitText(label, ribbon.width * 0.35, ribbon.height * 0.35);
    ribbonComponent.addChild(label);

    return ribbonComponent;
  }

  _buildTitleText() {
    return makeText(getCtaTitleTextConfig());
  }

  _buildQuests(cta) {
    // eslint-disable-next-line no-shadow
    const quests = new Phaser.Group(CI_API.game);

    questsConfig.forEach((questConfig, index) => {
      const quest = new Quest(cta, questConfig);
      quests.addChild(quest);
      quest.scale.set(LP(1, 1.3));
      quest.x += (quest.width + LP(55, 10)) * index;
    });

    quests.x = -quests.getBounds().x - quests.width / 2;
    quests.y = -quests.getBounds().y - quests.height / 2 + LP(15, 20);

    return quests;
  }
}

export class CTAView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._blocker = null;
    this.bindings = moduleBindings;

    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
    this.alpha = 0;

    this.build();
  }

  getGridConfig() {
    return this.bindings.gridConfigs.getCTAStyle1GridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    this._removeChildren();

    super.rebuild(config);

    if (!store.ad.cta || (store.ad.cta && !store.ad.cta.visible)) {
      return;
    }

    this.initialize();
  }

  buildPlayButton(config) {
    return this._buildButton(config, this._onPlayClick);
  }

  buildRetryButton(config) {
    return this._buildButton(config, this._onRetryClick);
  }

  build(config = this.getGridConfig()) {
    super.build(config);
  }

  initialize(blockerAlpha = 0.4) {
    this._buildBlocker(blockerAlpha);

    this.setChild('footer_bar', (this._footer = this._buildFooter(this.getCellByName('footer_bar').area)));
    this.setChild('popup', (this._popup = new CtaPopup(this)));
    this.setChild('characters', (this._characters = makeImage(getCtaCharactersImageConfig())));
    this.setChild('play_button', (this._button = this._buildPlayBtn()));

    this._playBtn.buttonPulsation();
  }

  show() {
    this.game.add.tween(this).to({ alpha: 1 }, 1000, Phaser.Easing.Sinusoidal.Out, true, 0);
  }

  _removeChildren() {
    this.removeChild(this._footer);
    this._footer = null;
    this.removeChild(this._popup);
    this._popup = null;
    this.removeChild(this._characters);
    this._characters = null;
    this.removeChild(this._button);
    this._button = null;
    this.removeChild(this._blocker);
    this._blocker = null;
  }

  _buildPlayBtn() {
    this._playBtn = this.buildPlayButton(getCtaStyle1PlayButtonConfig());
    return this._playBtn;
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this.initialize();
        this.show();
        break;
      default:
        break;
    }
  }

  _buildBlocker(alpha) {
    this._blocker = makePixel({ alpha });
    this._blocker.inputEnabled = true;
    this._blocker.input.priorityID = InputPriority.Cta;
    this._blocker.name = this.name;
    this.setChild('blocker', this._blocker);

    if (CI_API.Globals.PARAMS.cta_scrn_clickable) {
      this._blocker.events.onInputDown.add(this._onScreenClick, this);
    }
  }

  _buildButton(config, callback) {
    const btn = new Button(config);
    btn.onClick.add(callback, this);
    return btn;
  }

  _buildFooter(bounds) {
    const { width, height } = bounds;
    // const { width: wf, height: hf } = this.getCellByName('footer_bar').area;
    // return new Phaser.Group(this.game);
    return makeGradient({
      width,
      height,
      horizontal: 0,
      vertical: 1,
      colors: [
        {
          percent: 0,
          color: '#fced48'
        },
        {
          percent: 0.2,
          color: '#fced48'
        },
        {
          percent: 0.8,
          color: '#f5bb2b'
        },
        {
          percent: 1,
          color: '#f5bb2b'
        }
      ]
    });
  }

  _onScreenClick() {
    lego.event.emit(ViewEvents.CtaView.ScreenClick);
  }

  _onPlayClick() {
    lego.event.emit(ViewEvents.CtaView.PlayClick);
  }

  _onRetryClick() {
    lego.event.emit(ViewEvents.CtaView.RetryClick);
  }
}
