import { getItemBgImageConfig } from '../../../configs/image-configs';
import { ItemsConfig } from '../../../configs/items-config';
import { makeImage } from '../../../utils';
import { BeautyItemComponent } from './items/beauty-item-component';
import { BootsItemComponent } from './items/boots-item-component';
import { CandyItemComponent } from './items/candy-item-component';
import { CatItemComponent } from './items/cat-item-component';
import { ChristmasTreeItemComponent } from './items/christmas-tree-item-component';
import { CupItemComponent } from './items/cup-item-component';
import { HouseItemComponent } from './items/house-item-component';
import { PlantItemComponent } from './items/plant-item-component';
import { RapunzelItemComponent } from './items/rapunzel-item-component';
import { TreeItemComponent } from './items/tree-item-component';
import { WoodsItemComponent } from './items/woods-item-component';

export const ItemComponent = {
  tree: TreeItemComponent,
  // core
  coffee: CupItemComponent,
  boots: BootsItemComponent,
  candy: CandyItemComponent,

  sticks: WoodsItemComponent,
  house: HouseItemComponent,

  christmasSmall: PlantItemComponent,
  christmasLarge: ChristmasTreeItemComponent,

  rapunzel: RapunzelItemComponent,
  beauty: BeautyItemComponent,
  puss: CatItemComponent
};

export class ItemView extends Phaser.Sprite {
  constructor(uuid, type) {
    super(CI_API.game, 0, 0);

    this.dragStart = new Phaser.Signal();
    this.dragStop = new Phaser.Signal();
    this.dragUpdate = new Phaser.Signal();
    this.onClick = new Phaser.Signal();

    this._type = type;
    this._uuid = uuid;
    this._shape = new Phaser.Circle(0, 0, 60);
    this._highlightTween = null;

    this._build();
  }

  get uuid() {
    return this._uuid;
  }

  get shape() {
    return this._shape;
  }

  // @ts-ignore
  // eslint-disable-next-line class-methods-use-this
  get name() {
    return 'Item';
  }

  hide() {
    this.alpha = 0;
  }

  show() {
    this.alpha = 1;
  }

  startAlert() {
    this._bg.tint = 0xff0000;
    this._content.tint = 0xff0000;
  }

  stopAlert() {
    this._bg.tint = 0xffffff;
    this._content.tint = 0xffffff;
  }

  startMagnet(dX, dY) {
    this.stopMagnet();
    this._magnetTwn = this.game.add
      .tween(this._content)
      .to({ x: dX * 10, y: dY * 8 }, 300, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
  }

  stopMagnet() {
    this.game.tweens.remove(this._magnetTwn);
    this._content.position.set(0, 0);
  }

  startHighlight() {
    if (this._highlightTween) {
      return;
    }
    this.game.tweens.remove(this._highlightTween);
    this._bg.alpha = 0;
    this._bg.visible = true;
    this._highlightTween = this.game.add
      .tween(this._bg)
      .to({ alpha: 1 }, 350, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
  }

  stopHighlight() {
    this.game.tweens.remove(this._highlightTween);
    this._bg.alpha = 0;
    this._bg.visible = false;
    this._highlightTween = null;
  }

  _build() {
    this.addChild((this._bg = makeImage(getItemBgImageConfig())));
    this._bg.visible = false;
    // eslint-disable-next-line new-cap
    this.addChild((this._content = new ItemsConfig[this._type].view()));
  }

  makeDraggable() {
    this.inputEnabled = true;
    this.input.enableDrag();
    this.events.onDragStart.add(this._onDragStart, this, 0);
    this.events.onDragStop.add(this._onDragStop, this, 0);
  }

  removeDraggable() {
    this.inputEnabled = false;
    // this.input.disableDrag();
    this.events.onDragStart.remove(this._onDragStart, this);
    this.events.onDragStop.remove(this._onDragStop, this);
    this.events.onDragUpdate.remove(this._onDragUpdate, this);
  }

  _onDragStart() {
    this.bringToTop();
    this.events.onDragUpdate.add(this._onDragUpdate, this, 0);
    this.dragStart.dispatch(this);
  }

  _onDragStop(target, pointer) {
    this.events.onDragUpdate.remove(this._onDragUpdate, this);
    const {
      positionDown: { x: dX, y: dY },
      positionUp: { x: uX, y: uY }
    } = pointer;

    if (Math.abs(dX - uX) > 10 || Math.abs(dY - uY)) {
      this.dragStop.dispatch(this);
    } else if (CI_API.Globals.PARAMS.tap_to_merge) {
      this.onClick.dispatch(this);
    } else {
      this.dragStop.dispatch(this);
    }
  }

  _onDragUpdate() {
    this.dragUpdate.dispatch(this);
  }
}
