import { getBoardRapunzelSpineConfig, Spines } from '../../../../configs/spine-configs';
import { makeSpine } from '../../../../utils';
import { Container } from '../../../../utils/container';

export class RapunzelItemComponent extends Container {
  constructor() {
    super();
    const spine = makeSpine(getBoardRapunzelSpineConfig(0, 0));
    spine.setAnimationByName(0, Spines.BoardRapunzel.Animations.Animation, true);

    this.addChild(spine);
    this.scale.set(0.6);
  }
}
