/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getUIGridConfig } from '../../configs/grid-configs';
import { getCoinBarImageConfig, getMagicBarImageConfig } from '../../configs/image-configs';
import { getCoinsBarLabelTextConfig, getMagicBarLabelTextConfig } from '../../configs/text-configs';
import { ModelEvents } from '../../events/model-events';
import { makeImage, makeText } from '../../utils';
import { Container } from '../../utils/container';
import { LogoView } from '../foreground/logo-view';
import { PersistentCTAView } from './persistent-cta-view';

export class UIView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this._build();

    lego.event.on(ModelEvents.AdModel.PersistentCtaUpdate, this._onPersistentCtaUpdate, this);
  }

  get name() {
    return 'UIView';
  }

  getGridConfig() {
    return getUIGridConfig();
  }

  rebuild(conf) {
    if (!this.grid) {
      return;
    }

    super.rebuild(conf);

    this._persistentCtaView && this._persistentCtaView.buttonPulsation();
  }

  _build() {
    super.build(this.getGridConfig());
    this._buildCoinBar();
    this._buildMagicBar();
    this._buildLogo();
  }

  // LOGO
  _buildLogo() {
    this.setChild('logo', (this._logoView = new LogoView()));
  }

  // PERSISTENT
  _onPersistentCtaUpdate(persistentCta) {
    persistentCta ? this._buildPersistentCta() : this._destroyPersistentCta();
  }

  _buildPersistentCta() {
    this.setChild('p_cta', (this._persistentCtaView = new PersistentCTAView()));
    this._persistentCtaView.show();
  }

  _destroyPersistentCta() {
    this._persistentCtaView.destroy();
    this._persistentCtaView = null;
  }

  _buildCoinBar() {
    const coinBar = new Container();

    const coinBarBg = makeImage(getCoinBarImageConfig());
    coinBarBg.anchor.set(0.5);
    coinBar.addChild(coinBarBg);

    const coinsBarLabel = makeText(getCoinsBarLabelTextConfig());
    coinsBarLabel.anchor.set(0.5);
    coinBar.addChild(coinsBarLabel);

    this.setChild('coin_bar', coinBar);
  }

  _buildMagicBar() {
    const coinBar = new Container();

    const coinBarBg = makeImage(getMagicBarImageConfig());
    coinBarBg.anchor.set(0.5);
    coinBar.addChild(coinBarBg);

    const coinsBarLabel = makeText(getMagicBarLabelTextConfig());
    coinsBarLabel.anchor.set(0.5);
    coinBar.addChild(coinsBarLabel);

    this.setChild('magic_bar', coinBar);
  }
}
