import { CellAlign } from '@armathai/phaser2-grid';

export function getUIOldGridLandscapeConfig() {
  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'logo',
        bounds: { x: 0, height: 0.2 },
        padding: { x: 0.1, width: 1 - 0.2, y: 0.31, height: 1 - 0.3 - 0.11 }
      },
      {
        name: 'p_cta',
        bounds: { x: 0, height: 0.14, y: 0.84 },
        offset: { x: -120 },
        align: CellAlign.RightCenter
      },
      {
        name: 'coin_bar',
        bounds: { x: 0.02, width: 1 / 2 - 0.02, height: 0.08, y: 0.05 },
        align: CellAlign.LeftCenter
      },
      {
        name: 'magic_bar',
        bounds: { x: 1 / 2, width: 1 / 2 - 0.02, height: 0.1, y: 0.05 },
        align: CellAlign.RightCenter
      }
    ]
  };
}

